import api from './api'

/**
 * LocalStorage 
 */
class LocalStorage {
  /**
   * 
   * @param {String} key   
   */
  getItem (key) {
    // 
    if (api.platform === 'my') {
      return api.getStorageSync({key: key}).data
    }
    return api.getStorageSync(key)
  }

  /**
   * 
   * @param {String} key   
   * @param {Any} value 
   */
  setItem (key, value) {
    // 
    if (api.platform === 'my') {
      return api.setStorageSync({key: key, data: value})
    }
    return api.setStorageSync(key, value)
  }
}

// 
export default new LocalStorage(api)
